body {
  /* padding: 1.5em;
  background: #f5f5f5; */
/* width:100%;
height:100vh; */
  font-family: Arial, sans-serif;
}

.home_maincontainer{
  width:100%;
  height:55rem;
  display: flex;
  justify-content: center;
  margin-top: 10%;
  /* align-items: center; */
  /* background-color: rgb(0, 0, 0); */
}


.table-container {
  width: 70%;
  height:auto; 
  overflow-x: auto;
}

table {
  border: 1px #a39485 solid;
  font-size: 0.9em;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.25);
  width: 100%;
  border-collapse: collapse;
  border-radius: 5px;
  overflow: hidden;
  margin-bottom: 1.5em;
}

th {
  /* text-align: left; */
  background: #181817;
  color: #fff;
}

thead {
  font-weight: bold;
}

td, th {
  padding: 1em 0.5em;
  vertical-align: middle;
}

td {
  border-bottom: 1px solid rgba(0, 0, 0, 0.1);
  background: #fff;
}

a {
  color: #73685d;
}

tr :hover {
  background-color: #d2d2d2;
}

@media all and (max-width: 768px) {
  table, thead, tbody, th, td, tr {
    display: block;
  }

  th {
    text-align: right;
  }

  table {
    position: relative;
    padding-bottom: 0;
    border: none;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.2);
  }

  thead {
    float: left;
    white-space: nowrap;
  }

  tbody {
    overflow-x: auto;
    overflow-y: hidden;
    position: relative;
    white-space: nowrap;
  }

  tr {
    display: inline-block;
    vertical-align: top;
  }

  th {
    border-bottom: 1px solid #a39485;
  }

  td {
    border-bottom: 1px solid #e5e5e5;
  }
   
}
